*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a,
button {
  text-decoration: none;
  color: inherit;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.space {
  justify-content: space-between;
}

.center {
  align-items: center;
  justify-content: center;
}

.uppercase {
  text-transform: uppercase;
}

.y-underline {
  border-bottom: 8px solid hsl(51, 100%, 49%);
}

.r-underline {
  border-bottom: 8px solid hsl(7, 99%, 70%);
}

.text-bottom {
  align-items: center;
  justify-content: end;
}

.navbar {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  font-family: "Barlow", sans-serif;
  font-size: 18px;
  color: hsl(0, 0%, 100%);
  padding: 1.2rem 1.6rem;
}
.navbar__burger {
  display: none;
}
.navbar__logo {
  margin-right: auto;
}
.navbar__links a {
  padding-right: 3em;
}
.navbar__btn {
  border: none;
  border-radius: 18px;
  padding: 0.6em 1.4em;
  background-color: hsl(0, 0%, 100%);
  color: hsl(167, 40%, 24%);
  font-family: "Barlow", sans-serif;
  font-weight: 600;
}
.navbar__btn:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.2);
  color: hsl(0, 0%, 100%);
}
@media (max-width: 800px) {
  .navbar__links, .navbar__btn {
    display: none;
  }
  .navbar__burger {
    display: inline-block;
  }
}

.hero {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("../images/desktop/image-header.jpg");
  background-size: cover;
  background-position: bottom;
}
@media (max-width: 800px) {
  .hero {
    background-image: url("../images/mobile/image-header.jpg");
  }
}
@media (max-width: 600px) {
  .hero {
    height: 90vh;
  }
}
@media (max-width: 420px) {
  .hero {
    height: 80vh;
  }
}
@media (max-width: 600px) {
  .hero__title {
    white-space: normal;
    text-align: center;
  }
}
.hero__title h1 {
  font-family: "Fraunces", serif;
  color: hsl(0, 0%, 100%);
  font-size: 4rem;
  letter-spacing: 5px;
  margin-top: -30%;
}
@media (max-width: 1200px) {
  .hero__title h1 {
    font-size: 3rem;
  }
}
@media (max-width: 420px) {
  .hero__title h1 {
    font-size: 2.5rem;
  }
}

.img-block {
  flex: 1;
  max-width: 50%;
  height: 100vh;
}
.img-block--1 {
  background-image: url("../images/desktop/image-transform.jpg");
  background-size: cover;
  background-position: center;
}
@media (max-width: 1200px) {
  .img-block--1 {
    background-image: url("../images/mobile/image-transform.jpg");
  }
}
.img-block--2 {
  background-image: url("../images/desktop/image-stand-out.jpg");
  background-size: cover;
  background-position: center;
}
@media (max-width: 1200px) {
  .img-block--2 {
    background-image: url("../images/mobile/image-stand-out.jpg");
  }
}
.img-block--3 {
  background-image: url("../images/desktop/image-graphic-design.jpg");
  background-size: cover;
  background-position: center;
  padding-bottom: 9%;
  text-align: center;
}
@media (max-width: 1200px) {
  .img-block--3 {
    background-image: url("../images/mobile/image-graphic-design.jpg");
    text-align: left;
  }
}
.img-block--4 {
  background-image: url("../images/desktop/image-photography.jpg");
  background-size: cover;
  background-position: center;
  padding-bottom: 9%;
  text-align: center;
}
@media (max-width: 1200px) {
  .img-block--4 {
    background-image: url("../images/mobile/image-photography.jpg");
    text-align: left;
  }
}

.text-block {
  max-width: 50%;
  margin-bottom: 10%;
}

.text-container {
  max-width: 50%;
  max-height: 50%;
}

.text-container h2 {
  padding-bottom: 2.5rem;
  font-family: "Fraunces", serif;
  font-size: 3rem;
}
@media (max-width: 1200px) {
  .text-container h2 {
    font-size: 2.5rem;
    padding-bottom: 2rem;
  }
}
.text-container p {
  font-family: "Barlow", sans-serif;
  font-size: 18px;
  padding-bottom: 2.5rem;
  line-height: 1.5;
}
@media (max-width: 1200px) {
  .text-container p {
    font-size: 1rem;
  }
}
.text-container a {
  border-radius: 5px;
  width: 7.5rem;
  line-height: 0.5;
  font-family: "Fraunces", serif;
  font-weight: 700;
}

.testimonials {
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.testimonials__container {
  margin: 10%;
}
.testimonials__container h2 {
  margin-bottom: 7%;
  color: hsl(232, 10%, 55%);
  font-family: "Fraunces", serif;
  text-align: center;
  letter-spacing: 5px;
}
.testimonials__grid {
  display: flex;
  justify-content: space-between;
}
.testimonials__item {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.testimonials__item img {
  border-radius: 50%;
  max-width: 20%;
}
.testimonials__item .person {
  margin-top: 2rem;
}
.testimonials__item .person .name {
  padding-bottom: 0.4rem;
  font-family: "Fraunces", serif;
  font-weight: 900;
}
.testimonials__item .person .ocupation {
  color: hsl(210, 4%, 67%);
  font-size: 14px;
  font-family: "Barlow", sans-serif;
}
.testimonials__text {
  max-width: 80%;
  padding: 1rem;
  margin-top: 2rem;
  font-family: "Barlow", sans-serif;
  color: hsl(212, 27%, 19%);
}